<!--
  A toggleable settings menu that can be accessed by the cog icon
  on the right side of the screen.
 -->

<template>
  <div
    class="settings-menu"
    :class="{ opened: settingsMenuOpened }"
    v-on-clickaway="clickedOutside"
  >
    <transition
      enter-active-class="fadeInRight"
      leave-active-class="fadeOutRight"
    >
      <div class="icons" v-if="settingsMenuOpened">
        <span
          v-for="button in buttons"
          :key="button.class"
          class="icon"
          :class="[button.class, { active: button.active }]"
          :title="button.title"
          :disabled="button.disableable && noPlayers"
          @click="button.action"
        />
      </div>
    </transition>
    <span
      class="fa fa-cog"
      :style="settingsMenuOpened && { transform: 'rotate(-90deg)' }"
      @click="$store.dispatch('toggleSettingsMenu')"
    />
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import { mixin as clickaway } from 'vue-clickaway'

export default {
  name: 'SettingsMenu',
  mixins: [clickaway],
  data () {
    return {
      buttons: [
        { class: 'poison', title: 'Poison counter', action: this.togglePoisonCounters, active: this.poisonCountersVisible, disableable: true },
        { class: 'commander', title: 'Commander damage', action: this.toggleCommanderCounters, active: this.commanderCountersVisible, disableable: true },
        { class: 'dice', title: 'Roll dice', action: () => this.$router.push('/dice') },
        { class: 'fa fa-user-plus', title: 'Add player', action: this.addPlayer },
        // { class: 'fa fa-paint-brush', title: 'Change colors', action: () => console.log('a') },
        { class: 'reset-game text-button', title: 'Reset current game', action: this.resetCurrentGame },
        { class: 'new-game text-button', title: 'Start new game', action: this.startNewGame }
        // { class: 'fa fa-bar-chart', title: 'Game statistics', action: () => this.$router.push('/statistics') }
      ]
    }
  },
  mounted () {
    // console.log(this.settingsMenuOpened, this.poisonCountersVisible)
  },
  updated () {
    // console.log(this.settingsMenuOpened, this.poisonCountersVisible)
  },
  watch: {
    poisonCountersVisible () {
      // console.log(this.settingsMenuOpened, this.poisonCountersVisible)
    }
  },
  computed: {
    ...mapState({
      poisonCountersVisible: state => state.app.poisonCountersVisible,
      settingsMenuOpened: state => state.app.settingsMenuOpened,
      commanderCountersVisible: state => state.app.commanderCountersVisible
    }),
    ...mapGetters(['numberOfPlayers', 'noPlayers'])
  },
  methods: {
    ...mapActions([
      'addPlayer',
      'startNewGame',
      'resetCurrentGame',
      'togglePoisonCounters',
      'toggleCommanderCounters'
    ]),
    clickedOutside () {
      if (this.settingsMenuOpened) {
        this.$store.dispatch('toggleSettingsMenu', false)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.settings-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
	position: absolute;
	bottom: 50%;
	width: 100vw;
	background-color: transparent;
	font-size: 2.5em;
	padding: 5px;
	text-align: right;
	transition: background-color 0.3s ease, opacity 0.4s ease;
  transform: translateY(50%);
  overflow: hidden;
  pointer-events: none;
  @media (max-width: 400px) {
    font-size: 2em;
  }
}

.icon,
.fa {
  color: black;
  opacity: 0.5;
  cursor: pointer;
  pointer-events: auto;
}

.settings-menu.opened {
	background-color: rgba(0, 0, 0, 0.1);
}

.fa-cog {
  margin-left: auto;
  cursor: pointer;
  transition: all 0.3s;
}

.fa-cog:active {
  opacity: 0.85;
}

.icons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.icon {
	display: inline-block;
	transition: all 0.3s;
  &:not(:last-of-type) {
    margin-right: 0.3em;
    @media (max-width: 400px) {
      margin-right: 0.2em;
    }
  }

  &:active {
    opacity: 1;
    transform: scale(1.1);
  }
  &.active {
    opacity: 1;
  }
}

.poison,
.commander,
.dice {
	height: 1em;
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: contain;
}

.poison {
	background-image: url(../assets/poison.svg);
	width: 26px;
}

.commander {
	background-image: url(../assets/commander.svg);
	width: 35px;
}

.dice {
  background-image: url(../assets/dice.svg);
  width: 35px;
}

.text-button {
  display: flex;
	position: relative;
	max-width: 1.2em;
  height: 1em;
	border: 2px solid;
	border-radius: 5px;
	font-weight: 600;
	text-align: center;
	text-transform: uppercase;
  &::after {
    content: "";
    display: block;
    margin: auto;
    font-size: 0.3em;
  }
}

.new-game::after {
  content: "New game";
}

.reset-game::after {
  content: "Reset game";
}

[disabled] {
	opacity: 0.15;
	pointer-events: none;
}
</style>



